.content {
  display: flex;
  justify-content: center;
  height: 100%;
  overflow-y: scroll;
}

.showScrollBar {
  &::-webkit-scrollbar {
    display: inline-block;
  }
}

.hideScrollBar {
  &::-webkit-scrollbar {
    display: none;
  }
}

.container {
  width: 50%;
  height: 100%;

  & .title {
    font-size: 2em;
    font-weight: bold;
  }
}