.content {
  display: flex;
  justify-content: center;
  height: 100%;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: inline-block;
  }
}

.container {
  width: 50%;
  height: 100%;

  & .profile {
    width: 100%;
    height: 50vh;
    background-size: cover;
    position: absolute;
    top:0;
    left: 0;
    z-index: -1;
  }

  & .title {
    font-size: 2em;
    font-weight: bold;
    margin-top: 60vh;
  }
}